.container{
    max-width: 1440px;
    margin: 0 auto;
}

.small-container{
    max-width: 1140px;
    margin: 0 auto;
}

.highlighted-btn{
    padding: 3px 25px;

    transition: .5s;

    color: #fcde26;
    border: 5px #fcde26 solid;
    border-radius: 20px;
    background-color: #253957;
}

input[type='submit']{
    cursor: pointer;
}

.highlighted-btn:hover{
    color: #253957;
    background-color: #fcde26;
}

.underline-link{
    position: relative;

    cursor: pointer;

    color: #2f56b0;
}

.underline-link:after{
    position: absolute;
    right: 100%;
    bottom: .14em;
    left: 0;

    content: '';

    border-top: .1em solid #2f56b0;
    border-radius: 1em;
}

.underline-link:hover:after{
    right: 0;
}

.link-anim{
    padding: 8px 15px;

    transition: .5s;

    border-radius: 20px;
}

.link-anim:hover{
    transition: .5s;

    color: #000;
    border-color: #fff;
    background-color: #fff;
}

.sup-btn{
    padding: 3px 10px;

    transition: .3s;

    border: 2px solid #000;
    border-radius: 20px;
}

.sup-btn:hover{
    transition: .3s;

    border: 2px solid #fff;
    background-color: #fff;

    filter: invert(1);
}

.sup-btn-circled{
    align-self: center;
    min-height: 30px;
    min-width: 30px;
    max-height: 30px;
    max-width: 30px;
    font-size: 20px;
    font-weight: 700;
    transition: .3s;

    border: 2px solid #000;
    border-radius: 50%;
}

.sup-btn-circled:hover{
    transition: .3s;

    border: 2px solid #fff;
    background-color: #fff;

    filter: invert(1);
}

.icon-anim{
    height: 50px;

    transition: .5s;

    border-radius: 30%;
}

.icon-anim:hover{
    transition: .5s;

    border-radius: 50%;
    background-color: #000;

    filter: invert(1);
}

.rounded{
    border-radius: 16px;
}

.top-rounded{
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
}

.bottom-rounded{
    border-bottom-right-radius: 16px;
    border-bottom-left-radius: 16px;
}
.f-pre{
    font-size: 1.1rem;
}

.f-medium{
    font-size: 1.25rem;
}

.f-large{
    font-size: 1.7rem;
}

.f-extra-large{
    font-size: 2.5rem;
}

.text-overflow{
    overflow: hidden;

    white-space: pre;
    text-overflow: ellipsis;
}

.headed{
    font-size: 1.5rem;

    color: #47485a;
}

.highlighted{
    font-size: 1.3rem;
    font-weight: 600;
}

.fixed{
    position: fixed;
}

.no-fix-blur-box.active{
    z-index: 3;
    top: 0;
    left: 0;

    display: block;

    width: 100%;
    height: 100%;

    backdrop-filter: blur(8px);
}

.sup{
    font-size: .9rem;

    color: #d2d2d2;
}

.bg-transparent{
    background: transparent;
}

.white{
    color: #fff;
}

.green{
    color: #18ac00;
}

.red{
    color: #e40000;
}

.yellow{
    color: #c99100;
}

.flex{
    display: flex;
    flex-direction: row;
}

.bg-light-black{
    background-color: #1e1f26;
}

.bg-blue-gray{
    background-color: #253957;
}

.bg-blue-black{
    background-color: #001e4b;
}

.bg-violet-black{
    background-color: #150050;
}

.bg-violet-gray{
    background-color: #351124;
}

.input-list{
    position: relative;

    display: flex;
    align-items: left;
    flex-wrap: wrap;
}

.input-list__input-block{
    flex-grow: 1;

    margin: 5px 0 10px 0;
    padding: 7px 10px;
}

.list-input-field__el-block{
    margin-left: 5px;
}

.blur-box.muted{
    display: none;
}

.blur-box.active{
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;

    display: block;

    width: 100%;
    height: 100%;

    backdrop-filter: blur(3px);
}

.check-container{
    display: flex;
    justify-content: space-between;
}

.check-container input[type='checkbox']{
    margin-right: 10px;
}

.address__popup-block{
    position: relative;

    display: flex;
}

.map-popup{
    position: absolute;
    z-index: 3;
    top: 45px;
    right: -250px;

    width: 500px;
    height: 420px;
    padding: 20px;

    border: 1px solid #000;
    background-color: #fff;
}

.map-popup .leaflet-container{
    height: inherit !important;
    max-height: 380px;
}

.map-popup__closer-btn{
    position: absolute;
    top: 5px;
    right: 10px;
}

.appearence-anim{
    animation: appear-opacity 1s both;
}

.appearence-anim-height{
    animation: appear-height 1s both;
}

.my-node-enter{
    height: 0;
}
.my-node-enter-active{
    transition: opacity 200ms;

    height: 100px;
}
.my-node-exit{
    height: 100px;
}
.my-node-exit-active{
    transition: opacity 200ms;

    height: 0;
}


.full-width{
    width: 100% !important;
}

.error-block{
    text-align: center;
    vertical-align: middle;
    border: 1px solid #666666;
    padding: 20px;
}

@keyframes appear-opacity{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes appear-height{
    0%{
        height: 0;
    }
    100%{
        height: 100%;
    }
}

@keyframes appear-top{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

